(function() {
  const body = document.querySelector('body');
  const navigation = document.querySelector('.nav');
  const navigationBtn = document.querySelector('.btn-menu');

  initMobileMenu();
  initBtnTop();
  initAnchorNav();
  headerStickyInit();

  /**
   * Show navigation menu on mobile
   */
  function initMobileMenu() {
    navigationBtn.addEventListener('click', () => {
      body.classList.toggle('body--overflow');
      navigation.classList.toggle('nav--opened');
      navigationBtn.classList.toggle('btn-menu--close');
    });
  }

  /**
   * Anchor link navigation
   */
  function initAnchorNav() {
    const navLinks = document.querySelectorAll('.nav__link');
    const v = 0.3;
    let t;
    let start;

    [].forEach.call(navLinks, (link) => {
      link.addEventListener(
        'click',
        function(e) {
          e.preventDefault();

          for (let i = 0; i < navLinks.length; i++) {
            navLinks[i].classList.remove('nav__link--active');

            this.classList.add('nav__link--active');
          }

          if (navigation.classList.contains('nav--opened')) {
            body.classList.remove('body--overflow');
            navigation.classList.remove('nav--opened');
            navigationBtn.classList.remove('btn-menu--close');
          }

          let w = window.pageYOffset;
          let hash = this.href.replace(/[^#]*(.*)/, '$1');

          (t = document.querySelector(hash).getBoundingClientRect().top),
            (start = null);

          requestAnimationFrame(step);

          function step(time) {
            if (start === null) {
              start = time;
            }

            let progress = time - start;
            let r =
              t < 0
                ? Math.max(w - progress / v, w + t)
                : Math.min(w + progress / v, w + t);
            window.scrollTo(0, r);

            if (r != w + t) {
              requestAnimationFrame(step);
            } else {
              location.hash = hash;
            }
          }
        },
        false
      );
    });
  }

  /**
   * Scroll top btn
   */
  function initBtnTop() {
    const btnScrollTop = document.querySelector('.scroll-top');
    let scrolled;
    let timer;

    window.addEventListener(
      'scroll',
      (e) => {
        if (window.pageYOffset > '700') {
          btnScrollTop.style.display = 'block';
        } else {
          btnScrollTop.style.display = 'none';
        }
      },
      false
    );

    btnScrollTop.addEventListener('click', () => {
      scrolled = window.pageYOffset;
      scrollToTop();
    });

    function scrollToTop() {
      if (scrolled > 0) {
        window.scrollTo(0, scrolled);
        scrolled -= 100;
        timer = setTimeout(scrollToTop, 20);
      } else {
        clearTimeout(timer);
        window.scrollTo(0, 0);
      }
    }
  }

  /** */
  const contactsBlock = document.querySelector('#contact');
  const contactsBtn = document.querySelector('.btn-to-contacts');
  let coord = contactsBlock.getBoundingClientRect().top + pageYOffset;

  contactsBtn.addEventListener('click', () => {
    window.scrollTo(0, coord);
  });

  /**
   * Header sticky
   */
  function headerStickyInit() {
    const header = document.querySelector('.header');

    window.addEventListener(
      'scroll',
      () => {
        window.pageYOffset > 30
          ? header.classList.add('header--sticky')
          : header.classList.remove('header--sticky');
      },
      false
    );
  }

  /**
   * Reviews section slider
   * Swiper slider
   */
  const mySwiper = new Swiper('.swiper-container', {
    pagination: {
      el: '.swiper-pagination'
    }
  });
})();
